
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
body {
  margin: 0;
  background-color: #F7F7F5;
  font-size: 0.24rem;
  padding-bottom: .9rem;
}
ul {
  list-style-type: none;
}
ul:after {
  display: table;
  content: '';
  clear: both;
}
h1,
h2,
h3,
h4,
p,
ul,
li {
  margin: 0;
  padding: 0;
}
i {
  font-style: normal;
}
a {
  text-decoration: none;
}
.top {
  line-height: .9rem;
  border-bottom: 1px solid #e2e2e2;
  position: relative;
  background-color: #fff;
  height: 0.9rem;
}
.top li {
  display: block;
  height: 0.9rem;
}
.top .title {
  display: inline-block;
  font-weight: normal;
  text-align: center;
  width: 100%;
  font-size: .3rem;
}
.top .left {
  position: absolute;
  top: 0;
  left: 0;
  width: .9rem;
  text-align: center;
}
.top .right {
  position: absolute;
  top: 0;
  right: 0;
  width: .9rem;
  text-align: center;
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
}
.overlay .overlay-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}
.overlay .box {
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  bottom: 0.2rem;
  left: 50%;
  width: 95%;
  margin-left: -47.5%;
  box-sizing: border-box;
  overflow: hidden;
  opacity: 1;
}
.overlay .box h2 {
  font-size: .24rem;
  color: #C0BFBF;
  border-bottom: 1px solid #e2e2e2;
  padding: .2rem;
  font-size: normal;
}
.overlay .box ul {
  padding: 0 0.2rem;
}
.overlay .box li {
  line-height: .9rem;
  text-transform: capitalize;
  text-align: center;
}
.overlay .box i {
  color: #C0BFBF;
  display: inline-block;
}
.overlay .box span {
  text-align: center;
  display: block;
  background-color: #f5f5f5;
  color: #C0BFBF;
  line-height: .7rem;
}
.overlay-enter-active,
.overlay-leave-active {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.overlay-enter-active .overlay-bg,
.overlay-leave-active .overlay-bg {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.overlay-enter-active .box,
.overlay-leave-active .box {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.overlay-enter .overlay-bg,
.overlay-leave-to .overlay-bg {
  opacity: 0 ;
}
.overlay-enter .box,
.overlay-leave-to .box {
  -webkit-transform: translate(0, 100%);
          transform: translate(0, 100%);
}
.icon {
  background-size: 4.5rem;
  background-image: url('http://amiechenn.github.io/amiechenn/domevue/my-porject/static/icon.png');
}



